import {Injectable} from '@angular/core';
import {observable, Observable, Subscriber} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class DataTransferService {

  private image = '';

  private imageSubscriber: Subscriber<string>;
  public imageChangeNotifier: Observable<string>;


  public getLoginImage(): Observable<string> {
    if (!this.imageChangeNotifier) {
      this.imageChangeNotifier = new Observable<string>(observer => {
        this.imageSubscriber = observer;
      });
    }

    return this.imageChangeNotifier;
  }

  public changeLoginImage(value: string) {
    this.imageSubscriber.next(value);
  }

}
